import axios from 'axios';
import React, {useState, useEffect} from 'react';

export default function App(){
  const [file, setFile] = useState()
  const [output, setOutput] = useState({})
  const [done, setDone] = useState(false)
  const word_chars = "0123456789abcdefghijklmnopqrstuvwxyz-'’"
  const connect_chars = ' —.,():;?!"'

  useEffect(()=>{
    if(file != null){
      console.log('file changed')
      parseFile()
    }
  },[file])

  const parseFile = () => {
    console.log('parsing')
    let current = '';
    for (let i = 0; i < file.length; i++){
      if(word_chars.includes(file[i].toLowerCase())){
        console.log('char:', file[i].toLowerCase())
        current = current.concat(file[i].toLowerCase())
      }
      else if(connect_chars.includes(file[i])){
        if(!connect_chars.includes(current)){
          console.log('current:', current)
          if(output[current] == null){
            output[current] = 1
          }
          else{
            output[current]++
          }
          setOutput(output)
          current = ''
        }
      }
      else{
      }
    }
    if(!connect_chars.includes(current)){
      if(output[current] == null){
        output[current] = 1
      }
      else{
        output[current]++
      }
      setOutput(output)
      current = ''
    }
    setOutput(output)
    current = ''
    setDone(true)
  }
  const onFileChange = async (e) => {
    e.preventDefault()
    const reader = new FileReader()
    reader.onload = async (e) => {
      const text = (e.target.result)
      setFile(text)
    };
    reader.readAsText(e.target.files[0])
  }
  const onTextChange = async (e) => {
    setFile(e.target.value)
  }
  const sortFunction = (a,b) => {
    if (a[1] === b[1]) {
        return 0;
    }
    else {
        return (a[1] < b[1]) ? -1 : 1;
    }
  }
  let fileOutput
  if (done){
    var sortable=[];
  	for(var key in output)
  		if(output.hasOwnProperty(key))
  			sortable.push([key, output[key]])
    sortable.sort(sortFunction)
    sortable = sortable.reverse()
    fileOutput=
      <div>
        <p>{JSON.stringify(sortable)}</p>
      </div>
  }
  else{
    fileOutput=
      <div>
        <h1>idk</h1>
      </div>
  }
  return(
    <div>
      <h1>Upload a text file or copy paste</h1>
      <input type="file" onChange={(e) => onFileChange(e)}/>
      <input type="text" onChange={(e)=> onTextChange(e)}/>
      {fileOutput}
    </div>
  )
}
